import React from 'react';
import 'react-image-lightbox/style.css';
import { HashRouter } from 'react-router-dom';
import { HelmetProvider } from 'react-helmet-async';
import { Provider as ReduxProvider } from 'react-redux';
// import { PersistGate } from 'redux-persist/lib/integration/react';

import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';

import { store, persistor } from '../redux/store';
import CustomElement from './CustomElement';
import App from '../App';

const ATTRIBUTES = {
  locale: "locale"
};

class ChatWidgetElement extends CustomElement {
  static TAG = 'mail-widget'; // kebab-case

  constructor() {
    super(ATTRIBUTES, ChatWidgetElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <HelmetProvider>
      <ReduxProvider store={store}>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <HashRouter>
            <App {...attrs} />
          </HashRouter>
        </LocalizationProvider>
      </ReduxProvider>
    </HelmetProvider>
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}


if (!customElements.get(ChatWidgetElement.TAG)) {
  customElements.define(ChatWidgetElement.TAG, ChatWidgetElement);
}