export function conversationHashCode(participants) {
    const hashcode = participants.map(p => p.id).sort().join('-');
    console.log(`
    HashCode: ${hashcode}
    participants to hash`, participants)
    return hashcode;
}

export function mapParticipants(recipient) {
    return {
        id: recipient.id,
        name: recipient.full_name,
        email: recipient.email,
        username: recipient.id,
        status: 'online',
        lastActivity: new Date(),
        phone: '01123456789',
        address: '488 express ave, Mississauga, ON'
    };
};

export function mapMessage(conversation) {
    // console.log('conversation in message: ', conversation)
    const message = {
        id: conversation.id,
        unread: conversation.flags.find(flag => flag == 'read') ? 0 : 1,
        participants: conversation.display_recipient.map(mapParticipants),
        body: conversation.content,
        contentType: conversation.content_type,
        senderId: conversation.sender_id,
        createdAt: new Date(conversation.timestamp * 1000),
        attachments: []
    };
    const avatar_url = conversation.avatar_url;
    const senderParticipant = message.participants.find(p => p.id == message.senderId)
    senderParticipant.avatar = avatar_url;
    return message;

};
export function getParticipantIds(participants) {
    return participants.map(p => p.id)
};

export function compareArrays(a, b) {
    a.forEach(aEl => {
        if (!b.include(aEl)) {
            return true
        }
    })
}
export function countUnreadMessages(messages) {
    let count = 0;
    messages.forEach(message => {
        count += message.unread;
    })
    return count;
}
export function groupMessagesInConversations(messages, currentUserID = '8') {
    console.log('current user id: ', currentUserID)
    const groupedConversation = {};

    messages.forEach(message => {
        const participantIds = JSON.stringify(getParticipantIds(message.participants));
        if (!groupedConversation[participantIds]) groupedConversation[participantIds] = []

        groupedConversation[participantIds].push(message);
    });
    const conversations = [];
    // console.log('i am here')
    // console.log(groupedConversation)
    Object.values(groupedConversation).forEach(groupedC => {
        // console.log('i am here')
        const conversation = {};
        conversation.id = conversationHashCode(groupedC[0].participants.filter(p => p.id != currentUserID));
        conversation.participants = groupedC[0].participants;
        conversation.type = 'ONE_TO_ONE';

        const unreadCount = countUnreadMessages(groupedC);
        conversation.unreadCount = unreadCount;

        conversation.messages = groupedC;

        conversations.push(conversation);
    })
    // console.log(conversations);
    console.log('Mapped conversations: ', conversations)
    return conversations;
}

