import { createSlice } from '@reduxjs/toolkit';
// utils

import { getChatClient } from '../../api/ChatClient';
import { mapMessage, groupMessagesInConversations, countUnreadMessages } from '../../api/bff';

// ----------------------------------------------------------------------

function objFromArray(array, key = 'id') {
  return array.reduce((accumulator, current) => {
    accumulator[current[key]] = current;
    return accumulator;
  }, {});
}

const initialState = {
  isLoading: false,
  error: false,
  contacts: { byId: {}, allIds: [] },
  conversations: { byId: {}, allIds: [] },
  activeConversationId: null,
  currentUser: {},
  participants: [],
  recipients: [],
  apiKey: '',
  email: '',
  realm: 'https://demo-dw.zulipchat.com'
};

const slice = createSlice({
  name: 'chat',
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    updateMessages(state, action) {
      const { message, conversationId } = action.payload;
      if (message.senderId != state.currentUser.user_id) {
        state.conversations.byId[conversationId].messages.push(message);
        state.conversations.byId[conversationId].unreadCount = countUnreadMessages(state.conversations.byId[conversationId].messages);
      }
    },

    getCurrentUserSuccess(state, action) {
      const currentUser = action.payload;
      state.currentUser = currentUser;
    },

    // GET CONTACT SSUCCESS
    getContactsSuccess(state, action) {
      const contacts = action.payload;

      state.contacts.byId = objFromArray(contacts);
      state.contacts.allIds = Object.keys(state.contacts.byId);
    },

    // GET CONVERSATIONS
    getConversationsSuccess(state, action) {
      const conversations = action.payload;

      // get all private messages
      const pm = conversations.filter(pm => pm.type === 'private').map(mapMessage);
      const mappedPMConversations = groupMessagesInConversations(pm, state.currentUser.user_id);
      console.log('mapped conversations in state: ', mappedPMConversations)
      state.conversations.byId = objFromArray(mappedPMConversations);
      state.conversations.allIds = Object.keys(state.conversations.byId);
    },

    // GET CONVERSATION
    getConversationSuccess(state, action) {
      let conversation = action.payload;
      // console.log(conversation)
      // console.log('i am in conversation success')
      const mappedPMConversations = groupMessagesInConversations(conversation.map(mapMessage), state.currentUser.user_id);
      // console.log('i am in conversation success after mapping')

      // console.log(mappedPMConversations)

      conversation = mappedPMConversations.length > 0 ? mappedPMConversations[0] : null;
      // console.log(conversation)
      if (conversation) {
        state.conversations.byId[conversation.id] = conversation;
        state.activeConversationId = conversation.id;
        if (!state.conversations.allIds.includes(conversation.id)) {
          state.conversations.allIds.push(conversation.id);
        }
      } else {
        state.activeConversationId = null;
      }
    },

    // ON SEND MESSAGE
    onSendMessage(state, action) {
      const conversation = action.payload.value;
      const { conversationId, messageId, message, contentType, attachments, createdAt, senderId } = conversation;

      const newMessage = {
        id: messageId,
        body: message,
        contentType,
        attachments,
        createdAt,
        senderId
      };

      state.conversations.byId[conversationId].messages.push(newMessage);
    },

    markConversationAsReadSuccess(state, action) {
      const { conversationId } = action.payload;
      const conversation = state.conversations.byId[conversationId];
      if (conversation) {
        conversation.unreadCount = 0;
      }
    },

    // GET PARTICIPANTS
    getParticipantsSuccess(state, action) {
      const conversationKey = action.payload;
      const participants = state.conversations.byId[conversationKey].participants;
      console.log('participants in participant success: ', participants)
      state.participants = participants;
    },

    // RESET ACTIVE CONVERSATION
    resetActiveConversation(state) {
      state.activeConversationId = null;
    },

    addRecipients(state, action) {
      const recipients = action.payload;
      state.recipients = recipients;
    },

    addApiKey(state, action) {
      console.log("Adding email address to redux store")
      const email = action.payload;
      state.email = email;
      if (email == 'abdulzeedo@gmail.com') {
        state.apiKey = '0v3U9HGDAEnddm1OJ2OXYoDDKeWZLwqg';
      }
      else {
        state.apiKey = 'l9O7Z6dcwuotlURP3yncH7s0g095Oygg';
      }
    }
  }
});

// Reducer
export default slice.reducer;

// Actions
export const { addRecipients, onSendMessage, updateMessages, resetActiveConversation, addApiKey } = slice.actions;

// ----------------------------------------------------------------------

export function getCurrentUser() {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const client = await getChatClient();

      // const response = await axios.get('/api/chat/contacts');
      const response = await client.users.me.getProfile();
      console.log('current user response: ', response);
      dispatch(slice.actions.getCurrentUserSuccess(response));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}


export function getContacts() {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const client = await getChatClient();

      // const response = await axios.get('/api/chat/contacts');
      const response = await client.users.retrieve({ client_gravatar: true });
      // console.log(response);
      dispatch(slice.actions.getContactsSuccess(response.data.contacts));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function getConversations() {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      // const response = await axios.get('/api/chat/conversations');
      const client = await getChatClient();
      const readParams = {
        anchor: "newest",
        num_before: 100,
        num_after: 0,
        client_gravatar: 'true'
      };
      const response = await client.messages.retrieve(readParams);
      dispatch(slice.actions.getConversationsSuccess(response.messages));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function getConversation(conversationKey) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    console.log('getting conversation')
    try {
      // const response = await axios.get('/api/chat/conversation', {
      //   params: { conversationKey }
      // });
      const client = await getChatClient();
      const readParams = {
        anchor: "newest",
        num_before: 100,
        num_after: 0,
        narrow: [
          { operator: "pm-with", operand: [parseInt(conversationKey)] }
        ],
      };
      console.log('here')
      const response = await client.messages.retrieve(readParams);
      dispatch(slice.actions.getConversationSuccess(response.messages));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function markConversationAsRead(conversationId) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      // await axios.get('/api/chat/conversation/mark-as-seen', {
      //   params: { conversationId }
      // });
      // const client = await zulipInit(config);
      // const addflag = {
      //   messages: conversationIds,
      //   flag: "read",
      // };
      // const response = await client.messages.flags.add(addflag);
      dispatch(slice.actions.markConversationAsReadSuccess({ conversationId }));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function getParticipants(conversationKey) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      // const response = await axios.get('/api/chat/participants', {
      //   params: { conversationKey }
      // });
      dispatch(slice.actions.getParticipantsSuccess(conversationKey));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function sendMessage(value) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const client = await getChatClient();
      const params = {
        to: value.recipients,
        type: "private",
        content: value.message,
      };
      const response = await client.messages.send(params);
      dispatch(slice.actions.onSendMessage({ response, value }));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}
